@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #2d3436;
  color: #c7ecee;
}

.container {
  display: grid;
  padding: 2em;
  grid-template-columns: 1fr 1fr 1fr;
}

.container-energy {
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 1px solid #c7ecee;
  margin: .5em .5em;
}

.round {
  margin-bottom: 0;
}

.container-winlose {
  grid-column: 2;
  display: flex;
  flex-direction: column;
  padding: .85em;
  margin: .5em .5em;
  border: 1px solid #c7ecee;
}

.Winlose {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  text-align: center;
}
.winratio {
  grid-column: 2;
  margin-top: .25em;
  font-size: 1.1em;
}

button {
  padding: .5em;
  border: none;
  margin: .5em;
  background-color:#515662;
  color: #c7ecee;
  font-size: .88em;
  font-weight: bold;
  text-transform: uppercase;
}

button:hover {
  transform: scale(1.02);
}

@media (max-width: 1335px) {
  .container {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 912px) {
  .container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  .container-energy {
    margin: .5em 0;
  }
  .container-winlose {
    grid-column: auto;
    margin: .5em 0;
    grid-row: 2/3;
  } 
}